export default {
   data(){  
      return{ 
          form:this.placeGroup?
          {
              ...this.placeGroup
          }
          :
          {
              topic:"",
              description:"",
              icon:"",
              context_path:""
              
          }
      }

    },
    mounted: function(){    

      $('#placeGroupForm').validate({
          rules: {
            topic: {
              required: true,
              maxlength: 500 
            } 
          },
          messages: {
            topic: {
              required: "กรุณากรอกกลุ่มสถานที่",
              maxlength: "ขนาดตัวอักษรต้องไม่เกิน 500 ตัวอักษร" 
            } 
          },
          errorElement: 'span',
          errorPlacement: function (error, element) {
            error.addClass('invalid-feedback');
            element.closest('.form-group').append(error);
          },
          highlight: function (element, errorClass, validClass) {
            $(element).addClass('is-invalid');
          },
          unhighlight: function (element, errorClass, validClass) {
            $(element).removeClass('is-invalid');
          },
          ignore: ".note-editor *"
        });

      $(".textarea").summernote({ height: 500, focus: false });
    },  
    props:{
      placeGroup:{
          type:Object,
          required:false
      } 
    },
    methods:{   
      onSubmit(evt){    
          if($("#placeGroupForm").valid()){
            this.$emit('placeGroupData', this.form); 
          }else{
            console.log("validate is failed");
          }
      },
      onReset(evt){ 
        evt.preventDefault();
        this.form.topic="";
        this.form.description="";
        this.form.icon="";
        this.form.context_path="";
      }
    }
}

